import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';


import { ModalController } from 'ionic-angular';
import { LoadingController } from 'ionic-angular';
import { Events } from 'ionic-angular';
import { ApiProvider } from '../../providers/api/api';

import { AlertController } from 'ionic-angular';

/**
 * Generated class for the OpinionesPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-opiniones',
  templateUrl: 'opiniones.html',
})
export class OpinionesPage {

  constructor(public navCtrl: NavController, public navParams: NavParams, public modalCtrl: ModalController, public loadingCtrl: LoadingController,public events: Events, public apiProvider: ApiProvider, public alertCtrl: AlertController) {
  }

  ionViewDidLoad() {
    this.section = "one";
    console.log('ionViewDidLoad OpinionesPage');


        this.apiProvider.verificarLogin()
    .then(data => { 

      console.log(data);

      if(data){

      this.dataUser = data;
      //this.menuActivo = true;
      }
      else{
      console.log('error');
      // this.menuActivo = false;
      } 
      
      this.getOpiniones(this.dataUser.idCliente);
     });




  }

  filterFav(user: any) {
  console.log(user);
    return user.estado == 1
  }


  filterFav2(user: any) {
  console.log(user);
    return user.estado == 2
  }


  getOpiniones(idCliente) {

		let dataE = {idCliente:idCliente};
      console.log(dataE);
        this.apiProvider.getOpiniones(dataE)
      .then(data => {
       console.log(data);
              if(data){

         this.opiniones = data || [];
       }
       else{console.log('Ha ocurrido un error');}

       
      });
  }

}



